.locale {
  width: 6.875rem;
  height: 2.375rem;
  position: absolute;
  top: 0.625rem;
  right: 0.625rem;
  font-size: 0.4375rem;
  line-height: 1.25rem;
  border-radius: 0.3125rem;
  color: #333;
  text-align: center;
}
.locale .dropdown {
  width: 6.875rem;
  background-color: rgba(255, 255, 255, 0.6);
  border-radius: 0.3125rem;
}
.locale .dropdown .dropdown-toggle {
  width: 100%;
  background: transparent;
  border: none;
  color: #333;
}
.locale .dropdown-menu {
  min-width: 6.875rem;
  background: none;
  background-color: #d8d8d8;
  border: 0.03125rem solid #fff;
  opacity: 0.8;
}
.locale ul.lan-list {
  padding: 0;
  margin: 0 auto;
  width: 6.875rem;
}
